import React, { Component } from "react";
import { Grid, Table } from "semantic-ui-react";
import moment from "moment";
import OrderItem from "./OrderItem";
const formatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "THB",
  minimumFractionDigits: 0
});
export default class MyMenu extends Component {
  state = {};

  render() {
    return (
      <Table.Row>
        <Table.Cell colSpan="8">
          <Table basic stackable>
            <Table.Row>
              <Table.Cell width={16}>
                <Grid columns={2} basic padded>
                  <Grid.Column width={10}>
                    <p>
                       / Customers:{" "}
                      {this.props.order.address.firstName +
                        " " +
                        this.props.order.address.lastName}
                    </p>
                    <p>
                       / Address:
                      {" " +
                        this.props.order.address.addr +
                        " " +
                        this.props.order.address.distric +
                        " " +
                        this.props.order.address.province +
                        " " +
                        this.props.order.address.postcode}
                    </p>
                    <p>
                      {" : "}
                      {(() => {
                        switch (this.props.order.status) {
                          case "successful":
                            return <span></span>;
                          case "sended":
                            return <span></span>;
                          case "cancel":
                            return <span></span>;
                          default:
                            return <span></span>;
                        }
                      })()}
                    </p>
                  </Grid.Column>
                  <Grid.Column width={6}>
                    <p> / No: {this.props.order.id}</p>
                    <p>
                       / Date:
                      {moment
                        .unix(this.props.order.createdAt / 1000)
                        .format("lll")}
                    </p>
                  </Grid.Column>
                </Grid>
              </Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell colSpan={16}>
                <Table celled padded>
                  <Table.Row>
                    <Table.Cell colSpan={1}> Item</Table.Cell>
                    <Table.Cell colSpan={11}> Description</Table.Cell>
                    <Table.Cell colSpan={1}> Quantity</Table.Cell>
                    <Table.Cell colSpan={1}>/ Unit Price</Table.Cell>
                    <Table.Cell colSpan={2}> Amount</Table.Cell>
                  </Table.Row>
                  <OrderItem order={this.props.order} />
                </Table>
              </Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell floated="right" colSpan={6}>
                <Table padded>
                  <Table.Row>
                    <Table.Cell> TOTAL</Table.Cell>
                    <Table.Cell>
                      {formatter.format(this.props.order.total)} 
                    </Table.Cell>
                  </Table.Row>
                  <Table.Row>
                    <Table.Cell> NET AMOUNT</Table.Cell>
                    <Table.Cell>
                      {formatter.format(this.props.order.total)} 
                    </Table.Cell>
                  </Table.Row>
                </Table>
              </Table.Cell>
            </Table.Row>
          </Table>
        </Table.Cell>
      </Table.Row>
    );
  }
}
