import React, { Component } from "react";
import gql from "graphql-tag";
import { Mutation } from "react-apollo";
import OrderItem from "./OrderItem";
import CancelOrder from "./CancelOrder";
import Receipt from "../../Receipt/Receipt";
import {
  Button,
  Segment,
  Message,
  Header,
  Icon,
  Grid,
  Divider,
  Modal
} from "semantic-ui-react";
import moment from "moment";

import ReactToPrint from "react-to-print";
const formatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "THB",
  minimumFractionDigits: 0
});
const CREATE_PAYMENT_MUTATION = gql`
  mutation createPayment($id: ID!) {
    createPayment(id: $id) {
      message
    }
  }
`;

class Order extends Component {
  state = {
    showError: false,
    isLogged: false
  };
  render() {
    const { errorMessage } = this.state;
    const id = this.props.order.id;
    return (
      <>
        {this.state.showError && (
          <Segment basic textAlign="center">
            <Message
              warning
              onDismiss={e => this.setState({ showError: false })}
              compact
            >
              {errorMessage}
            </Message>
          </Segment>
        )}

        <Segment basic textAlign="center">
          <OrderComponent order={this.props.order} />
          {this.props.order.status === "wait" && (
            <Mutation
              mutation={CREATE_PAYMENT_MUTATION}
              variables={{
                id
              }}
              onCompleted={data => this._confirm(data)}
              onError={error => this._error(error)}
            >
              {mutation => (
                <Button.Group fluid>
                  <Button positive onClick={mutation}>
                    .
                  </Button>
                  <Button.Or />
                  <Modal
                    trigger={<Button negative></Button>}
                    basic
                    size="small"
                  >
                    <CancelOrder
                      id={this.props.order.id}
                      refetch={this.props.refetch}
                    />
                  </Modal>
                </Button.Group>
              )}
            </Mutation>
          )}
        </Segment>
        {this.props.order.status !== "wait" && (
          <Modal trigger={<Button fluid></Button>}>
            <Modal.Header></Modal.Header>
            <Modal.Content>
              <ReactToPrint
                trigger={() => (
                  <Button fluid>
                    
                  </Button>
                )}
                content={() => this.componentRef}
              />
              <Receipt
                order={this.props.order}
                ref={el => (this.componentRef = el)}
              />
            </Modal.Content>
          </Modal>
        )}
      </>
    );
  }
  editorText = htmltext => {
    this.setState({
      productDescriptionHtml: htmltext
    });
    console.log(htmltext);
  };
  toggleError = () => {
    this.setState((prevState, props) => {
      return { showError: true };
    });
    console.log(this.state.productPublished);
  };
  onDismiss = () => {
    this.setState((prevState, props) => {
      return { showError: false };
    });
  };
  _confirm = async data => {
    const message = data.createPayment;
    console.log(message);
    window.location = message.message;
  };
  _error = async error => {
    //alert(error);
    console.log(this.state.productImage);
    this.setState({ errorMessage: error.message });
    this.toggleError();
  };
  _saveUserData = token => {
    //localStorage.setItem(AUTH_TOKEN, token)
  };
}

class OrderComponent extends Component {
  render() {
    return (
      <Segment basic textAlign="center">
        <Divider horizontal>
          <Header as="h3">
            <Icon name="ordered list" />
             #{this.props.order.id}
          </Header>
        </Divider>
        <Grid columns={3} divided>
          <Grid.Row>
            {!this.props.order.status || this.props.order.status === "wait" ? (
              <Grid.Column>
                <Header>
                  <Icon name="calendar alternate outline" />
                  
                </Header>
                <Header>
                  {moment
                    .unix(this.props.order.createdAt / 1000)
                    .add(1, "d")
                    .format("llll")}
                  <Header.Subheader></Header.Subheader>
                </Header>
              </Grid.Column>
            ) : (
              <Grid.Column>
                <Header>
                  <Icon name="calendar alternate outline" />
                  
                </Header>
                <Header>
                  {moment
                    .unix(this.props.order.updatedAt / 1000)
                    .add(1, "d")
                    .format("llll")}
                </Header>
              </Grid.Column>
            )}

            <Grid.Column>
              <Header>
                <Icon name="money bill alternate outline" />
                
              </Header>
              <Header>{formatter.format(this.props.order.total)} </Header>
            </Grid.Column>
            <Grid.Column>
              <Header></Header>
              <Header>
                {(() => {
                  switch (this.props.order.status) {
                    case null:
                      return <Header color="yellow"></Header>;
                    case "sended":
                      return <Header color="blue"></Header>;
                    case "successful":
                      return <Header color="green"></Header>;
                    case "failed":
                      return <Header color="red"></Header>;
                    case "cancel":
                      return <Header color="red"></Header>;
                    default:
                      return <Header color="yellow"></Header>;
                  }
                })()}
              </Header>
            </Grid.Column>
          </Grid.Row>
        </Grid>
        <Divider horizontal>
          <Header as="h3"></Header>
        </Divider>
        <OrderItem order={this.props.order} />
        <Divider horizontal>
          <Header as="h3"></Header>
        </Divider>
        <Header as="h4">
          {" " +
            this.props.order.address.firstName +
            " " +
            this.props.order.address.lastName +
            " " +
            ". " +
            this.props.order.address.mobileNumber +
            " " +
            " " +
            this.props.order.address.addr +
            " " +
            this.props.order.address.distric +
            " " +
            this.props.order.address.province +
            " " +
            this.props.order.address.postcode}
        </Header>
      </Segment>
    );
  }
}
export default Order;
